@import "~@/styles/var";
@import "~@/styles/mixin";

.container {
  position: relative;
  height: 100vh;
  margin: 0 7.3%;
  .content {
    width: 100%;
    position: absolute;
    top: 11%;
    bottom: 18%;
    min-height: 328px;
    cursor: default;
    .left {
      margin-right: 440px;
      height: 100%;
      overflow: hidden;
      .btnGroup {
        width: 100%;
        height: 60px;
        .btn {
          display: inline-block;
          margin-right: 8px;
        }
      }
      .main {
        position: relative;
        width: 100%;
        height: calc(~"100% - 60px");
      }
    }
    .right {
      position: absolute;
      top: 0;
      right: 0;
      width: 340px;
      height: 100%;
      text-align: center;
      font-size: 14px;
      line-height: 24px;
    }
  }
  .controls {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 11%;

    // 使用精灵图
    .iconBtn {
      position: absolute;
      opacity: 0.8;
      background-image: url("~@/assets/player.png");
      background-image: -webkit-image-set(url("~@/assets/player.png") 1x, url("~@/assets/player@2x.png") 2x);
      cursor: pointer;
      &.iconPre {
        top: 4px;
        left: 0;
        width: 19px;
        height: 20px;
        background-position: 0px -30px;
      }
      &.iconPlay {
        top: 0;
        left: 65px;
        width: 21px;
        height: 29px;
        background-position: 0 0;
        &.on {
          background-position: -30px 0;
        }
      }
      &.iconStop {
        top: 0;
        left: 65px;
        width: 21px;
        height: 29px;
        background-position: 0 0;
      }
      &.iconNext {
        top: 4px;
        left: 132px;
        width: 19px;
        height: 20px;
        background-position: 0 -52px;
      }
      &.iconLoop {
        top: 4px;
        right: 378px;
        width: 26px;
        height: 25px;
        background-position: 0 -205px;
      }
      &.iconOrder {
        top: 4px;
        right: 377px;
        width: 23px;
        height: 20px;
        background-position: 0 -260px;
      }
      &.iconSingleLoop {
        top: 4px;
        right: 378px;
        width: 26px;
        height: 25px;
        background-position: 0 -232px;
      }
      &.iconRandom {
        top: 4px;
        right: 378px;
        width: 25px;
        height: 19px;
        background-position: 0 -74px;
      }
    }
    .progressWrap {
      position: relative;
      margin: 0 448px 0 208px;
      font-size: @font_size_medium;
      color: #fff;
      .musicInfo__name {
        padding-right: 90px;
        .no-wrap();
      }
      .musicInfo__time {
        position: absolute;
        top: 0;
        right: 0;
      }
      .playerProgress {
        position: relative;
        height: 2px;
        cursor: pointer;
        background: hsla(0, 0%, 100%, 0.15);
        .playerProgressInner {
          width: 90%;
          height: 2px;
          background: hsla(0, 0%, 100%, 0.2);
          margin-top: 6px;
        }
        .currentLoad {
          position: absolute;
          top: 0;
          left: 0;
          width: 60%;
          height: 2px;
          background: #fff;
          .iconDot {
            right: 0;
            top: 50%;
            right: 0;
            width: 10px;
            height: 10px;
            background-position: 0 -170px;
            opacity: 1;
            filter: none;
            margin-top: -5px;
            margin-right: -5px;
          }
        }
      }
    }
  }
}

/*遮罩*/
.playerBg,
.playerMask {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.playerMask {
  z-index: -1;
  background-color: @mask_color;
}

.playerBg {
  z-index: -2;
  background-image: url(~@/assets/music-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  filter: blur(12px); // 设置背景图 高斯模糊
  opacity: 0.7;
  transition: all 0.8s;
}
